    header {
        .nav-links {
            position: absolute;
            top: 12vh;
            height: 88vh;
            width: 100vw;
            left: 0;
            background-color: white;
            display: flex;
            flex-direction: column;
            align-items: start;
            justify-content: start;
            z-index: 3;
            transform: translateY(-140%);
            transition: transform 0.5s ease-in;
            padding: 0;
        }

        li {
            padding: 0.75em;

        }

        .logo {
            position: absolute;
            top: 20%;
            left: 50%;
            transform: translateX(-50%);
        }

        .hideLogo {
            display: none;
        }

        #inputSearch {

            width: 100%;

            z-index: 5;
        }

        .mobileHide {
            display: none;
        }

        .desktopHide {
            display: block;
        }



        .burger {
            display: block;
            margin-right: auto;
            cursor: pointer;
        }

        .nav-active {
            transform: translateY(0%);
        }

        .bottomLinks {
            position: absolute;
            bottom: 2%;
            display: flex;
            justify-content: center;

            a {
                padding: 0 1em;
            }

            img {
                padding: 0 0 1.5em 1em;
            }
        }
    }

/*-------------Sidebar-------------*/


    .herobanner {
        display: none;
    }
   
/***********product-details*****/
.col-lp
{
    display: none;
}

/*-----footer-----------*/
.f-bottom
{
 
    flex-direction: column ;
}

