{
    flex-direction: column;
}
/*-------banner--------*/

.banner{
    position: relative;
    flex-direction: column-reverse;


    .img{
        padding: 1em 1em 0em 1em;
        margin-bottom: 5em;
    }
}



.banner-left{
    padding: 1em;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%);

    margin: auto;

    width: 75%;

    h1{
       font-size: 2rem;
        color: $white;

        padding: 0 0 1em 0;
    }
    
   
}

